* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  scroll-behavior: smooth;
}


.welcome,
.projetcs,
.tech,
.contacts {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.navigation{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
  }

.navbar-container{
    background-color: teal;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
} 

.menu-icon{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.heart{
    color: red
}

.dedication{
    display:flex;
    position: relative;
    left: 25px;
    font-family: 'Lato', sans-serif;
    flex-wrap: wrap;
}

.sentence{

}

.nav-menu{
    background-color: light coral;
    margin-top: 0px;
    width: 250px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: fixed;
    left: -100%;
    transition: 850ms;
    /* z-index: 3; */
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
    z-index: 999;
}

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    margin-right: 0px;
    padding: 0 16px;
    border-radius: 4px;
  }

.nav-text a:hover {
    background-color: orange;
  }



ul {
    background: lightcoral;
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
}

.nav-button {
    color: white;
    background: none;
    border-bottom: 2px solid white;
    border-left: none;
    border-right: none;
    border-top: none;
    cursor: pointer;
    font-size: 15px;
  }
  
  .nav-button:hover {
    border-bottom: 2px solid orange;
  }
  
  .nav-button:focus {
    border-bottom: 2px solid orange;
    outline:0 !important; 
  }

  .nav-button:active{
    border-bottom: 2px solid orange;
  }
  



