  font-size: 0.75rem;
  display: flex;
  border-radius: 10px;
  align-items: center;
  justify-content: center;
  transition: all 1.0s ease;
  cursor: pointer;
}

.clicked{
  transform: scale(1.1);
  border-color: #ffc600;
  box-shadow: 0 0 1rem #ffc600;
}


